//import { ErrorOptions } from './types';

export interface ErrorOptions {
  message?: string,
  hints?: string,
  stack?: object
}

// Error utils
export class ErrorUtils {
  public error(err: Error, options?: ErrorOptions): object {
    const ret: object = err;

    if (options) {
      if (options.message) {
        ret['message'] = options.message;
      }
      if (options.hints) {
        ret['message'] = ret['message'] ?
          `${ret['message']}\nNote: ${options.hints}` : `Note: ${options.hints}`;
      }
    }
    return ret;
  }

  // method for invalid parameter type
  public invalidParameterType(parameter: string, wrongType: string, correctType: string): object {
    const msg = `Invalid type for parameter: ${parameter}. Instead of using ${wrongType} you should use ${correctType}.`;

    const typeError = new TypeError(msg);
    Error.captureStackTrace(typeError, this.invalidParameterType);
    return this.error(typeError);
  }

  public invalidParameterValue(parameter: string): object {
    const msg = `Invalid value for parameter: ${parameter}.`;

    const error = new Error(msg);
    Error.captureStackTrace(error, this.invalidParameterValue);
    return this.error(error);
  }

  // method for parameter out of range
  public outOfRange(parameter: string, lower: number, upper: number): object {
    const msg = `Invalid parameter: ${parameter} due to out or range. It should be within ${lower} and ${upper}.`;

    const rangeError = new RangeError(msg);
    Error.captureStackTrace(rangeError, this.outOfRange);
    return this.error(rangeError);
  }

  // method for missing parameter
  public missingParameter(parameter: string): object {
    const msg = `Missing parameter: ${parameter}`;

    const err = new Error(msg);
    Error.captureStackTrace(err, this.missingParameter);
    return this.error(err);
  }
}

const instance = new ErrorUtils();
export default instance;


