import * as AWS from "aws-sdk/global";

class Library implements Configurable {

    private options: object;

    constructor() {
        AWS.config.update({customUserAgent: 'Charlotte-js'});
    }

    config(options: MainConfig) {
        this.options = options;

        AWS.config.update({
            ...this.options,
            customUserAgent: 'Charlotte-js'
        });
    }
}

export interface MainConfig {
    region?: string,
    credentials?: AWS.Credentials
}

export interface Configurable {
    config(options: MainConfig): void
}

export * from './Auth';


const instance: Configurable = new Library();
export default instance;



