import React from 'react';
import {Auth} from '../auth';

/**
 * @param {React.Component} WrappedComponent 
 * @returns {React.Component}
 */
function WithAuthSignIn(WrappedComponent) {
    return class extends React.Component {
      constructor(props) {
        super(props);
  
        this.handleSignIn = this.handleSignIn.bind(this);
      }

      componentDidMount() {
      this.state = {
        username: null,
        password: null
    };
}
  
       handleSignIn = async(e) =>  {
          let username = this.state.username;
          let password = this.state.password;
          console.log("u and p = ", username, password);
        let signInDetails = await Auth.signIn(username, password);
        console.log("signInDetails are ", signInDetails);
      }
  
      render() {
        console.log('Rendering HOC');
  
        return (
          <div>
            <text onChange = {(event) => this.setState({username:event.target.value})}>username </text> <textarea/>
            <text onChange = {(event) => this.setState({password:event.target.value})}>password</text> <textarea/>
            <Button onClick = {this.handleSignIn()} >signIn</Button>
          </div>
          )
      }
    }
  }
  
  export default WithAuthSignIn;


