import * as AWS from 'aws-sdk/global';
import { default as CognitoAuth } from "./cognito-auth";
import { CognitoUser, ISignUpResult } from 'amazon-cognito-identity-js';
import { AuthOptions, SignUpParams } from './types';
import ErrorUtils from "../Core/ErrorUtils";


/**
* Provide authentication steps
*/
export class Auth {
  private authOptions: AuthOptions;
  private cognitoAuth: CognitoAuth;

  constructor(options: AuthOptions) {
    const { awsUserPoolsId, awsUserPoolsWebClientId, region, awsCognitoIdentityPoolId } = options;

    if (!awsUserPoolsId) {
      ErrorUtils.missingParameter('awsUserPoolsId');
    }
    if (!awsUserPoolsWebClientId) {
      ErrorUtils.missingParameter('awsUserPoolsWebClientId');
    }
    if (!awsCognitoIdentityPoolId) {
      ErrorUtils.missingParameter('awsCognitoIdentityPoolId');
    }

    this.authOptions = {
      awsUserPoolsId: awsUserPoolsId,
      awsUserPoolsWebClientId: awsUserPoolsWebClientId,
      region: region || AWS.config.region,
      awsCognitoIdentityPoolId: awsCognitoIdentityPoolId
    };

    this.cognitoAuth = new CognitoAuth(this.authOptions);
  }

  //TODO: Currently we are returning Promise<**CognitoInterface**> We should make sure this is want we want to return. We may want a middle layer that is either more helpful or more generic so we are not tied to Cognito.
  public async signUp(params: SignUpParams): Promise<ISignUpResult> {
    console.log("params for signup on library side ", params);
    return this.cognitoAuth.handleNewCustomerRegistration(params.username, params.password, params.SignupAttributes);
  }

  public async confirmSignUp(username: string, verificationCode: string): Promise<any> {
    console.log("username" + username);
    return this.cognitoAuth.handleSubmitVerificationCode(username, verificationCode);
  }

  public async resendSignUpMFA(username: string): Promise<"SUCCESS"> {
    return this.cognitoAuth.handleResendVerificationCode(username);
  }

  public async signIn(username: string, password: string): Promise<{}> {
    console.log("2: us" + typeof (username) + "pass" + typeof (password));
    console.log("username: " + username + " password: " + password);
    return this.cognitoAuth.handleSignIn(username, password);

  }


  public async submitSignInMFA(user: CognitoUser, verificationCode: string): Promise<any> {
    console.log("MFA code and username : ", verificationCode);
    console.log("user is ::: " + user);
    //user = JSON.parse(user);
    console.log("cog user : ", JSON.stringify(user));
    return this.cognitoAuth.sendMFAVerificationCode(user, verificationCode);
  }


  public async signOut(): Promise<void> {
    return this.cognitoAuth.handleSignOut();
  }

  public async forgotPassword(username: string): Promise<any | void> {
    return this.cognitoAuth.handleForgotPassword(username);
  }

  public async confirmMFAForgotPassword(username: string, verificationCode: string, newPassword: string): Promise<any> {
    return this.cognitoAuth.handleForgotPasswordReset(username, verificationCode, newPassword);
  }

  public getCredWithThirdParty(token: any, thirdPartyProvider: string): void {
    this.cognitoAuth.getCredWithThirdParty(token, thirdPartyProvider)
  }

}



